// https://stackoverflow.com/a/1144788/9324
export function replace(value: string, searchValue: string, replaceValue: string) {
    const escapedSearchValue = searchValue.replace(/([.*+?^=!:${}()|\[\]\/\\])/g, '\\$1');
    const replacedValue = value.replace(new RegExp(escapedSearchValue, 'g'), replaceValue);
    return replacedValue;
}
